body {
	text-align: center;
	padding: 5%;
	background-color: rgb(115, 182, 28);
	font-family: 'Poppins', sans-serif;
}

h4 {
	font-family: 'Pacifico', cursive;
}

.recipe-item {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	margin: 10px auto;
	padding: 15px;
	max-width: 600px;
	background-color: white;
	opacity: 0.8;
	border-radius: 3px;
}

.recipe-item:hover {
	-webkit-transform: scale(1.02);
	    -ms-transform: scale(1.02);
	        transform: scale(1.02);
	border-left: solid 5px rgb(20, 120, 193);
}

.recipe-text {
	display: inline-block;
	text-align: left;
	padding-right: 10px;
	width: 400px;
}

.recipe-img {
	display: inline-block;
	right: 0;
	width: 100px;
	height: 100px;
	-o-object-fit: cover;
	   object-fit: cover;
	border: 1px solid black;
	border-radius: 2px;
}

.star {
	font-size: 20px;
	cursor: pointer;
	margin-right: 15px;

}

.link {
	position: absolute;
	left: 30px;
	top: 3px;
}



